* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 16px;
  text-align: center;
  overflow-x: hidden;
}
li {
  text-decoration: none;
  list-style: none;
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
  border-bottom: 1px solid white;
}
.container {
  width: 100vw;
  height: 100vh;
  background: rgb(216, 221, 216);
  position: relative;
}

.app-wrapper {
  margin: 0 auto;
  padding: 1rem;
  background: rgb(192, 190, 190);
  width: 90vw;
  min-height: 90vh;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 960px;
  box-shadow: 0 1rem 5rem rgba(0, 0, 0, 0.7);
}

.header h1 {
  color: rgb(59, 59, 59);

  text-align: center;
  font-size: 2rem;
  padding: 1rem 0;
}

.balance h2 {
  color: hsl(12, 100%, 16%);
  font-size: 1.5rem;
  text-align: center;
}

.balance h3 {
  color: white;
  text-align: center;
  font-size: 2rem;
}

.income-expense {
  margin-top: 1rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  font-size: 1.5rem;
}

.plus,
.minus {
  padding: 1rem 0;
}

.plus h3 {
  color: rgb(4, 66, 4);
}
.plus {
  color: rgb(55, 158, 55);
}

.minus h3 {
  color: rgb(124, 8, 8);
}
.minus {
  color: rgb(240, 25, 25);
}

form {
  display: grid;
  grid-template-columns: 1fr;
  font-size: 1.5rem;
  text-align: left;
}

.expense {
  margin-top: 1rem;
}
input {
  padding: 0.5rem 0.5rem;
}

input[type='text'] {
  width: 100%;
}
input[type='number'] {
  width: 60%;
}
input[type='submit'] {
  width: 40%;
}
.income input[type='submit'] {
  background-color: #77c987;
}

.expense input[type='submit'] {
  background-color: #ff716e;
}

.transactions-income h2 {
  color: rgb(4, 66, 4);
  padding: 1rem;
  text-transform: capitalize;
}
.transactions-income {
  color: rgb(55, 158, 55);
}

.transactions-expense h2 {
  color: rgb(124, 8, 8);
  padding: 1rem;
  text-transform: capitalize;
}
.transactions-expense {
  color: rgb(240, 25, 25);
}

.transaction-amount {
  margin-left: auto;
  padding-right: 1rem;
}

.delete-btn {
  border: none;
  background-color: transparent;
  color: #d4403e;
  text-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.4);
  cursor: pointer;
  outline: none;
}
/* --------@Media breakpoints------- */
@media (min-width: 600px) {
  .form-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    font-size: 1.5rem;
    text-align: left;
    grid-gap: 2rem;
  }
  .transactions {
    width: 50%;
    padding: 1rem;
    font-size: 1.2rem;
    float: left;
  }
  .expense {
    margin-top: 0;
  }
  /* portrait tablets, portrait iPad, e-readers (Nook/Kindle), landscape 800x480 phones (Android) */
}
